<template>
	<div class="navigation">
		<div class="tbs">
			<div class="music">
				<img src="../../public/wyyimg/music.png" alt="">
				<span>网易云音乐</span>
			</div>
			<div class="app">
				<div class="zt">
					下载APP
				</div>
			</div>
		</div>
		<div class="navtbs">
			<div class="bt" :class="{'tab-active':item.isActive}"  v-for="(item,index) in todos" :key="index" @click="go(item)">{{item.title}}</div>
		</div>
		<router-view></router-view>
	</div>
</template>

<script>
// 	// @ is an alias to /src
	// import Slideshow from "@/components/Slideshow.vue";

	export default {
		name: "navigation",
		data() {
			return {
				todos: [
					{title: '推荐音乐',isActive: true,path: {name: 'videos'}},
					{title: '排行榜',isActive: false,path: {name: 'my'}},
					{title: '搜索',isActive: false,path: {name: 'search'}},
				],
				
			}
		},
		methods: {
			go(item) {
				if (item.isActive) {
					return;
				}
				for (var i = 0; i < this.todos.length; i++) {
					if (this.todos[i].isActive) {
						this.todos[i].isActive = false;
						break;
					}
				}
				item.isActive = true;
				this.$router.push(item.path)
			}
		}
	};
</script>
<style scoped lang="less">
	.navigation{
		width: 100%;
		.tbs{
			width: 100%;
			height: 65px;
			position: sticky;
			top:0;
			left:0;
			background-color: #D43C33;
			z-index: 50;
			.music{
				width: 200px;
				height: 65px;
				float: left;
				>img{
					margin-top: 15px;
					margin-left: 15px;
				}
				>span{
					display: block;
					font-size:20px;
					color: #FFFFFF;
					margin-left: 60px;
					margin-top:-33px ;
				}
			}
			.app{
				width: 100px;
				height: 65px;
				float: right;
				.zt{
					width: 80px;
					height: 30px;
					border: 1px solid #EA9E9A;
					border-radius: 15px;
					margin: 15px auto 0;
					color: #FFFFFF;
					text-align: center;
					line-height: 30px;
				}
			}
		}
		.navtbs{
			width: 100%;
			height: 40px;
			position: sticky;
			left:0;
			top: 65px;
			border-bottom: 1px solid #EBEBEB;
			background-color: #FFFFFF;
			z-index: 100;
			.tab-active{
				color: #D33A31;
				border-bottom: 1px solid #D33A31;
			}
			.bt{
				width: 30%;
				float: left;
				margin-left:10px;
				text-align: center;
				line-height: 40px;
			}
		}

	}
</style>
